import {AfterViewInit, Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { AlertController } from '@ionic/angular';
declare var google;

@Component({
  selector: 'app-especialista-sitio',
  templateUrl: './especialista-sitio.page.html',
  styleUrls: ['./especialista-sitio.page.scss'],
})
export class EspecialistaSitioPage implements OnInit, AfterViewInit {
  latitude: any;
  longitude: any;
  @ViewChild('mapElement', {static: true}) mapNativeElement: ElementRef;
  constructor(
    private geolocation: Geolocation,
    public alertController: AlertController
    ) { }

  ngOnInit() {
  }

  ngAfterViewInit(): void {
    this.geolocation.getCurrentPosition().then((resp) => {
      this.latitude = resp.coords.latitude;
      this.longitude = resp.coords.longitude;
      const map = new google.maps.Map(this.mapNativeElement.nativeElement, {
        center: {lat: -34.397, lng: 150.644},
        zoom: 15,
        zoomControl: false,
  mapTypeControl: false,
  scaleControl: false,
  streetViewControl: false,
  rotateControl: false,
  fullscreenControl: false
      });
      const infoWindow = new google.maps.InfoWindow;
      const pos = {
        lat: this.latitude,
        lng: this.longitude
      };
      infoWindow.setPosition(pos);
      infoWindow.setContent('Location found.');
      infoWindow.open(map);
      map.setCenter(pos);
    }).catch((error) => {
      console.log('Error getting location', error);
    });
  }

async presentAlert() {
    const alert = await this.alertController.create({
      header: 'Solicitud #12111 enviada',
      message: 'Su solicitud ha sido creada',
      buttons: ['Enterado']
    });

    await alert.present();
  }

}
